import React from 'react'
import Link from 'gatsby-link'

import {
  head
} from '../content/home'
import Head from '../components/Head'
import ProjectCard from '../components/ProjectCard'
import Container from '../components/Container'
import FadeInUp from '../components/FadeInUp'

const ProjectPage = () => (
  <Container>
    <Head {...head} />
    <FadeInUp>
      <article className="cf">
        <div className="fl w-50 tc">
          <ProjectCard 
            title="Incepto House"
            link="http://incepto.house/"
            imageUrl="http://incepto.house/img/maindoor.jpeg">
            incepto.house is newly formed community house in Menlo Park, CA. A creative and lovely place for like-minded people to share learnings and network networks. 
          </ProjectCard>
        </div>
        <div className="fl w-50 tc">
        </div>
      </article>
    </FadeInUp>
  </Container>
)

export default ProjectPage



